.FoodBook {
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 30%;
    &__header {
        margin-bottom: 10%;
        color: $second-color;
    }
    &__foodCategoriesBox {
        
        width: 90vw;
        height: 90vw;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }
    &__foodCategory {
        width: 45%;
        height: 45%;
        background-color: white;
        border-radius: 10px;
        box-shadow: -4px 0 20px -5px rgba(0, 0, 0, 0.384);
    }
}